<template>
	<div class="sub-container">
		<ul class="tabs sub">
			<li @click="goto('/admin/rolling-banner')">메인 배너</li>
			<li @click="goto('/admin/introduction')">운동친구 소개</li>
			<li @click="goto('/admin/admin-member')">운동친구 멤버</li>
			<li @click="goto('/admin/faq')">자주묻는질문</li>
			<li @click="goto('/admin/terms')" class="current">약관</li>
		</ul>

		<div class="contents-container">
			<!-- 운동 친구 소개 -->
			<p class="contents-title underline marB-20 body3">이용약관(웹)</p>

			<div class="input-container marB-40">
				<div class="img-upload-container">
					<p class="body3">웹 이미지</p>
					<div class="input-item align-top">
						<AddImageSingle
							v-if="webImage !== 'none'"
							:refName="'webImage'"
							:image="webImage"
							@webImage="setWebImage"
						/>

						<button class="outlined-btn" @click="updateTerm(webId, 'WEB')">
							저장
						</button>
					</div>
				</div>
				<div class="img-upload-container">
					<p class="body3">모바일 이미지</p>
					<div class="input-item align-top">
						<AddImageSingle
							v-if="mobileImage !== 'none'"
							:refName="'mobileImage'"
							:image="mobileImage"
							@mobileImage="setMobileImage"
						/>

						<button
							class="outlined-btn"
							@click="updateTerm(mobileId, 'MOBILE')"
						>
							저장
						</button>
					</div>
				</div>
				<LoadingSpinner :loading="isLoading" />
			</div>
		</div>
	</div>
</template>

<script>
import Store from '@/store';
import { mapGetters } from 'vuex';
import { fetchTermDetail, updateTerm } from '@/api/terms';
import AddImageSingle from '@/components/common/AddImageSingle';
import LoadingSpinner from '@/components/common/LoadingSpinner';

export default {
	created() {
		this.title = this.$route.params.title;
		this.fetchTermDetail(this.title);
	},
	components: { LoadingSpinner, AddImageSingle },
	computed: {
		...mapGetters(['web', 'mobile']),
	},
	data() {
		return {
			id: '',
			title: '',
			mobileImage: 'none',
			mobileId: '',
			webImage: 'none',
			webId: '',
			isLoading: false,
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async fetchTermDetail(title) {
			let { data } = await fetchTermDetail(title);
			this.webImage = data.web;
			this.webId = data.web.id;
			this.mobileImage = data.mobile;
			this.mobileId = data.mobile.id;
		},
		async updateTerm(id, type) {
			let data = new FormData();
			data.append('title', this.title);
			data.append('webMobile', type);

			if (type == 'WEB') {
				data.append('file', this.web);
			} else {
				data.append('file', this.mobile);
			}
			this.isLoading = true;
			try {
				await updateTerm(id, data);
			} catch (e) {
				console.log(e);
			} finally {
				this.isLoading = false;
			}
		},
		setWebImage(data) {
			Store.commit('SET_WEB', data);
			console.log(this.web);
		},
		setMobileImage(data) {
			Store.commit('SET_MOBILE', data);
			console.log(this.mobile);
		},
	},
};
</script>

<style></style>
